<template>
  <div>
    <!-- table  -->
    <div style="margin-top: 20px">
      <el-button @click="dialogFormVisible = true" size="mini"></el-button>
    </div>
    <div>
      <el-table
        :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column property="username" label="" width="120"></el-table-column>
        <el-table-column property="email" label="" width="240"></el-table-column>
        <el-table-column property="is_staff" label="" width="120"></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="editUser(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteUser(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <!-- Form -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.email" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </div>
      </el-dialog>
    </div>
    <div>
      <el-dialog title=""
                 :visible.sync="editFormVisible"
                 :close-on-click-modal="false"
                 class="edit-form">
        <el-form :model="editFormData"
                 label-width="80px"
                 ref="editForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.email" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="editFormVisible = false"></el-button>
          <el-button type="primary" @click="updateUser"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import apiUser from '../../api/user'

export default {
  // name: 'Main',
  data () {
    return {
      tableData: [],
      dialogFormVisible: false,

      editFormVisible: false, // 
      editFormData: {}, // 

      form: {
        username: '',
        password: '',
        email: ''
      },
      formLabelWidth: '120px',
      search: ''
    }
  },
  // 
  mounted: function () {
    this.getUser()
  },
  methods: {
    editUser (index, row) {
      this.editFormVisible = true
      this.editFormData = Object.assign({}, row) // 
    },
    // 
    deleteUser (index, row) {
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            apiUser.userDel(row).then((response) => {
              this.getUser()
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    },
    getUser () {
      // get
      apiUser.userList().then((response) => {
        // then  (this)
        this.tableData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // 
    addUser () {
      this.dialogFormVisible = false
      apiUser.userAdd(this.form).then((response) => {
        this.getUser()
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    updateUser () {
      // post
      this.editFormVisible = false
      apiUser.userUpdate(this.editFormData).then((response) => {
        this.getUser()
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped>

</style>



