import * as types from './types.js'

// token
// 1.vuexvuextoken
// 2.window.sessionStorage: sessionStoragesessionStoragetoken
// state.token,vue-routerdemo

const mutations = {
  [types.LOGIN]: (state, data) => {
    // token
    state.token = data
    window.sessionStorage.setItem('token', data)
  },
  [types.LOGOUT]: (state) => {
    // token
    state.token = null
    window.sessionStorage.removeItem('token')
  }
}

export default mutations



