<template>
  <div>
    <el-form :model="regForm" :rules="rules" label-width="100px" ref="regForm">
      <el-form-item label="" prop="username">
        <el-input v-model="regForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="regForm.password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPassword">
        <el-input v-model="regForm.checkPassword" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('regForm')"></el-button>
        <el-button @click="resetForm('regForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '../../api/login'

export default {
  data () {
    // 
    let validatePass1 = (rule, value, callback) => {
      // 6-16, , , , 
      let reg = /(?!^[0-9]+$)(?!^[A-z]+$)(?!^[^A-z0-9]+$)^[^\s\u4e00-\u9fa5]{6,16}$/
      if (!reg.test(value)) {
        callback(new Error('6-16'))
      } else {
        callback()
      }
    }
    // 
    let validatePass2 = (rule, value, callback) => {
      if (value !== this.regForm.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      regForm: {
        username: '',
        password: '',
        checkPassword: ''
      },
      rules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 16, message: '616', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {validator: validatePass1, trigger: 'blur'}
        ],
        checkPassword: [
          {required: true, message: '', trigger: 'blur'},
          {validator: validatePass2, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) { // 
          api.userRegister(this.regForm)
            .then(({data}) => {
              if (data.code === 1) {
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: ''
                })
              }
            })
        } else { // 
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
</style>



