<template>
  <div>
    <!-- table  -->
    <div>
      <el-table
        :data="tableData.filter(data => !search || data.task_name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column property="task_uuid" label="id" width="320"></el-table-column>
        <el-table-column property="task_name" label="" width="200"></el-table-column>
        <el-table-column :formatter="dateFormat" property="insert_time" label="" width="155">
        </el-table-column>
        <el-table-column property="username" label="" width="100"></el-table-column>
        <el-table-column property="task_describe" label=""></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="downFile(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteFile(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import apiFile from '../../api/file'
import moment from 'moment'

export default {
  data () {
    return {
      tableData: [],
      search: ''
    }
  },
  // 
  mounted: function () {
    this.getEmailFile() // 
  },
  methods: {
    // 
    dateFormat (row, column) {
      let date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    getEmailFile () {
      // get
      apiFile.fileList().then((response) => {
        // then  (this)
        this.tableData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // 
    downFile (index, row) {
      window.location.href = row.file_path
    },
    // 
    deleteFile (index, row) {
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            apiFile.fileDel(row).then((response) => {
              this.getEmailFile()
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    }
  }
}
</script>

<style scoped>
</style>



