<template>
  <div>
    <!-- table  -->
    <div style="margin-top: 20px">
      <el-button @click="dialogFormVisible = true" size="mini"></el-button>
    </div>
    <div>
      <el-table
        :data="tableData.filter(data => !search || data.db_type.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column property="db_type" label="" width="120"></el-table-column>
        <el-table-column property="host" label="" width="240"></el-table-column>
        <el-table-column property="describe" label="" width="240"></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="openEdit(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteSource(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <!-- Form -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.db_type" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.describe" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.host" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.port" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.database" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.user" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.password" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="addSource"> </el-button>
        </div>
      </el-dialog>
    </div>
    <div>
      <el-dialog title=""
                 :visible.sync="editFormVisible"
                 :close-on-click-modal="false"
                 class="edit-form">
        <el-form :model="editFormData"
                 label-width="80px"
                 ref="editForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="editFormData.db_type" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.describe" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.host" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.port" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.database" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.user" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.password" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="editFormVisible = false"></el-button>
          <el-button type="primary" @click="updateSource"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import api from '../../api/source'

export default {
  data () {
    return {
      options: [{
        value: 'MySQL',
        label: 'MySQL'
      }, {
        value: 'Hive',
        label: 'Hive'
      }],
      tableData: [],
      currentRow: null,

      dialogTableVisible: false,
      dialogFormVisible: false,

      editFormVisible: false, // 
      editFormData: {}, // 

      form: {
        db_type: '',
        describe: '',
        host: '',
        port: '',
        database: '',
        user: '',
        password: ''
      },
      formLabelWidth: '120px',
      search: ''
    }
  },
  // 
  mounted: function () {
    this.getSource()
  },
  methods: {
    // 
    deleteSource (index, row) {
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            api.sourceDel(row).then((response) => {
              this.getSource()
              // 
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    },
    // 
    openEdit (index, row) {
      this.editFormVisible = true
      this.editFormData = Object.assign({}, row) // 
    },
    getSource () {
      // get
      api.sourceList().then((response) => {
        // then  (this)
        this.tableData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // source
    addSource () {
      this.dialogFormVisible = false
      api.sourceAdd(this.form).then((response) => {
        this.getSource()
      }).catch((error) => {
        console.log(error)
      })
    },
    // 
    updateSource () {
      // post
      this.editFormVisible = false
      api.sourceUpdate(this.editFormData).then((response) => {
        this.getSource()
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped>

</style>



