<template>
  <div>
    <!-- table  -->
    <div>
      <el-table
        :data="PeriodicTaskData.filter(data => !search ||
        data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column property="uuid" label="#id" width="320"></el-table-column>
        <el-table-column property="name" label="" width="200"></el-table-column>
        <el-table-column :formatter="booleFormat" property="enabled" label="" width="100">
        </el-table-column>
        <el-table-column property="total_run_count" label="" width="100"></el-table-column>
        <el-table-column property="cron" label="" width="200"></el-table-column>
        <el-table-column :formatter="dateFormat" property="date_changed" label="" width="200">
        </el-table-column>
        <el-table-column property="description" label=""></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="editPeriodicTask(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deletePeriodicTask(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-dialog title=""
                 :visible.sync="checkPeriodicTask"
                 :close-on-click-modal="false">
        <el-form :model="PeriodicTaskInfo">
          <el-form-item label="" :label-width="labelWidth">
            <span style="float: left">{{ PeriodicTaskInfo.name }}</span>
          </el-form-item>
          <el-form-item label="" :label-width="labelWidth">
            <span style="float: left">{{ PeriodicTaskInfo.cron }}</span>
          </el-form-item>
          <el-form-item :formatter="dateFormat" label="" :label-width="labelWidth">
            <span style="float: left">{{ PeriodicTaskInfo.date_changed }}</span>
          </el-form-item>
          <el-form-item label="" :label-width="labelWidth">
            <span style="float: left">{{ PeriodicTaskInfo.args }}</span>
          </el-form-item>
          <el-form-item label="" :label-width="labelWidth">
            <span style="float: left">{{ PeriodicTaskInfo.description }}</span>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import apiTask from '../../api/task'
import moment from 'moment'

export default {
  // name: 'PeriodicTask',
  data () {
    return {
      PeriodicTaskData: [],
      search: '',
      checkPeriodicTask: false,
      PeriodicTaskInfo: {},
      labelWidth: '120px'
    }
  },
  // 
  mounted: function () {
    this.getPeriodicTask() // 
  },
  methods: {
    //  boole
    booleFormat (row, column) {
      let v = row[column.property]
      if (v === true) {
        return ''
      } else {
        return ''
      }
    },
    // 
    getPeriodicTask () {
      // get
      apiTask.periodicTaskList().then((response) => {
        // then  (this)
        this.PeriodicTaskData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // 
    dateFormat (row, column) {
      let date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    //  
    deletePeriodicTask (index, row) {
      // delete
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            apiTask.periodicTaskDel(row).then((response) => {
              this.getPeriodicTask()
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    },
    // 
    editPeriodicTask (index, row) {
      this.checkPeriodicTask = true
      this.PeriodicTaskInfo = row
    }
  }
}
</script>

<style scoped>
</style>



