<template>
  <div>
    <!-- table  -->
    <div>
      <el-table
        :data="tableData.filter(data => !search || data.task_name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column property="task_uuid" label="id" width="320"></el-table-column>
        <el-table-column property="task_name" label="" width="200"></el-table-column>
        <el-table-column property="status" label="" width="100"></el-table-column>
        <el-table-column :formatter="dateFormat" property="date_done" label="" width="155">
        </el-table-column>
        <el-table-column property="task_describe" label=""></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="infoLog(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteTaskLog(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-dialog title=""
                 :visible.sync="editFormVisible"
                 :close-on-click-modal="false"
                 class="edit-form">
        <el-form :model="editData"
                 label-width="100px">
          <el-form-item label="" width="200">
            <span style="float: left">{{ editData.task_name }}</span>
          </el-form-item>
          <el-form-item label="" width="100">
            <span style="float: left">{{ editData.status }}</span>
          </el-form-item>
          <el-form-item :formatter="dateFormat" label="" width="150">
            <span style="float: left">{{ editData.date_done }}</span>
          </el-form-item>
          <el-form-item label="" width="200">
            <span style="float: left">{{ editData.task_describe }}</span>
          </el-form-item>
          <el-form-item label="" width="200">
            <span style="float: left">{{ editData.username }}</span>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import apiTask from '../../api/task'
import moment from 'moment'

export default {
  // name: 'Main',
  data () {
    return {
      tableData: [],

      editFormVisible: false, // 
      editFormData: {}, // 
      editData: {},
      search: ''
    }
  },
  // 
  mounted: function () {
    this.getLogTask() // 
  },
  methods: {
    // 
    dateFormat (row, column) {
      let date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    getLogTask () {
      // get
      apiTask.taskLogList().then((response) => {
        // then  (this)
        this.tableData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    infoLog (index, row) {
      // 
      this.editFormVisible = true
      this.editData = Object.assign({}, row) // 
    },
    // 
    deleteTaskLog (index, row) {
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            apiTask.taskLogDel(row).then((response) => {
              this.getLogTask()
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    }
  }
}
</script>

<style scoped>
  .editor {
    height: 340px;
  }
</style>



