import instance from '../axios'

export default {
  // 
  userList () {
    return instance.get('/api/v1/users/')
  },
  // 
  userAdd (data) {
    return instance.post('/api/v1/users/', data)
  },
  // 
  userDel (data) {
    return instance.delete('/api/v1/users/' + data.id + '/')
  },
  // 
  userUpdate (data) {
    return instance.put('/api/v1/users/' + data.id + '/', data)
  }
}



