import instance from '../axios'

export default {
  // 
  userRegister (data) {
    return instance.post('/api-token-auth/', data)
  },
  // 
  userLogin (data) {
    return instance.post('/api-token-auth/', data)
  },
  //  token  User 
  tokenUserInfo () {
    return instance.post('/api-token-auth/')
  }
}



