<template>
  <div>
    <el-row>
      <el-col :span="10" :offset="7">
        <el-tabs v-model="activeName">

          <el-tab-pane label="" name="login">
            <el-form :model="loginForm" :rules="rules" label-width="100px" ref="loginForm">
              <el-form-item label="" prop="username">
                <el-input v-model="loginForm.username"></el-input>
              </el-form-item>
              <el-form-item label="" prop="password">
                <el-input v-model="loginForm.password" type="password"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('loginForm')"></el-button>
                <el-button @click="resetForm('loginForm')"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>

          <el-tab-pane label="" name="register">
            <Register></Register>
          </el-tab-pane>

        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import api from '../../api/login'
// 
import Register from './Register'
import store from '../../store/index'

export default {
  store,
  data () {
    return {
      activeName: 'login', // 
      loginForm: { // v-model
        username: '',
        password: ''
      },
      rules: { // 
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 16, message: '316', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) { // 
          api.userLogin(this.loginForm)
            .then(({data}) => { // data
              if (data.token) {
                this.$message({
                  type: 'success',
                  message: ''
                })
                this.$store.dispatch('UserLogin', data.token)
                // "/"this.$route.query.redirect
                let redirectUrl = decodeURIComponent(this.$route.query.redirect || '/')
                // 
                this.$router.push({
                  path: redirectUrl
                  // path: '/'
                })
              } else {
                this.$message({
                  type: 'info',
                  message: ''
                })
              }
            })
        } else {
          // 
          return false
        }
      })
    }
  },
  components: {
    Register
  }
}
</script>
<style scoped>

</style>



