<template>
  <div>
    <!-- table  -->
    <div>
      <el-table
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column property="name" label="" width="200"></el-table-column>
        <el-table-column :formatter="booleFormat" property="task_status" label="" width="100"></el-table-column>
        <el-table-column :formatter="dateFormat" property="update_time" label="" width="155">
        </el-table-column>
        <el-table-column property="describe" label=""></el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="editTask(scope.$index, scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteTask(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-dialog title=""
                 :visible.sync="editFormVisible"
                 :close-on-click-modal="false"
                 class="edit-form">
        <el-form :model="editFormData"
                 label-width="80px"
                 ref="editForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="editFormData.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 10}"
              placeholder=""
              v-model="editFormData.describe">
            </el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="editFormData.db_source_id" placeholder="">
              <el-option
                v-for="item in db_source_list"
                :key="item.id"
                :label="item.host"
                :value="item.id">
                <span style="float: left">{{ item.host }}</span>
                <span style="float: right; color: #8492a6; font-size: 13px">{{ item.db_type }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label-width="formLabelWidth">
            <monaco-editor class="editor" v-model="editFormData.code_context" language="sql">
            </monaco-editor>
            <el-button-group>
              <el-button type="primary" @click="format" icon="el-icon-edit"></el-button>
              <el-button type="primary" @click="cleanCode" icon="el-icon-delete"></el-button>
            </el-button-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="editFormVisible = false"></el-button>
          <el-button type="primary" @click="updateTask"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import apiTask from '../../api/task'
import apiSource from '../../api/source'
import moment from 'moment'
import MonacoEditor from 'vue-monaco'
import sqlFormatter from 'sql-formatter'

export default {
  components: {
    MonacoEditor
  },
  // name: 'Main',
  data () {
    return {
      tableData: [],
      db_source_list: null,

      editFormVisible: false, // 
      editFormData: {}, // 

      formLabelWidth: '120px',
      search: ''
    }
  },
  // 
  mounted: function () {
    this.getTask() // 
    this.getDBSource() // 
  },
  methods: {
    format () {
      /*  */
      this.editFormData.code_context = sqlFormatter.format(this.editFormData.code_context)
    },
    cleanCode () {
      this.editFormData.code_context = ''
    },
    //  boole
    booleFormat (row, column) {
      let v = row[column.property]
      if (v === true) {
        return ''
      } else {
        return ''
      }
    },
    getDBSource () {
      // get
      apiSource.sourceList().then((response) => {
        // then  (this)
        this.db_source_list = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // 
    dateFormat (row, column) {
      let date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    // 
    editTask (index, row) {
      this.editFormVisible = true
      this.editFormData = Object.assign({}, row) // 
    },
    // 
    deleteTask (index, row) {
      this.$alert('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        callback: action => {
          if (action === 'confirm') {
            // 
            apiTask.taskDel(row).then((response) => {
              this.getTask()
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    },
    // 
    getTask () {
      // get
      apiTask.taskList().then((response) => {
        // then  (this)
        this.tableData = response.data
      }).catch((error) => {
        // catch 
        console.log(error)
      })
    },
    // 
    updateTask () {
      // post
      this.editFormVisible = false
      apiTask.taskUpdate(this.editFormData).then((response) => {
        this.getTask()
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped>
  .editor {
    height: 340px;
  }
</style>



